$(document).ready(function() {

    sliderMain();
    sliderCategory();
    sliderCategories();
    sliderOtherCategories();
    sliderBudgetInfo();
    sliderProject();
    sliderQuestions2()


    // Menu
    $('.header__menu').click(function () {
        $('.menu').toggleClass('menu--active')
        $('body').toggleClass('fixed-body')
    })

    $('.menu-close').click(function () {
        $('.menu').removeClass('menu--active')
        $('body').removeClass('fixed-body')
    })

    // Before - After
    $(".before-after").twentytwenty({
        default_offset_pct: 0.5,
        no_overlay: true,
    });

    // Accordion
    $(".show").click((function(){
        $(this).toggleClass("show--active"),
        $(this).siblings(".hidden").slideToggle()
    }));

    // Select
    $(".custom_select select").niceSelect()

    // Tabs
    $(".account__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".account__tab").removeClass("account__tab--active")
        $(this).addClass("account__tab--active")

        $(this).parents(".account__inner").find(".account-cards").removeClass("account-cards--active")
        $(this).parents(".account__inner").find(`.account-cards[data-content-path="${path}"]`).addClass("account-cards--active")
    })

    // Menu2
    $('.page-top__burger').click(function () {
        $('.page-top__menu-links').toggleClass('page-top__menu-links--active')
    })

    let burger = $('.page-top__burger')
    let menu = $('.page-top__menu-links')

    $(document).mouseup(function (e) {
        if ( !burger.is(e.target) && burger.has(e.target).length === 0 &&
            !menu.is(e.target) && menu.has(e.target).length === 0

        ) {
            menu.removeClass('page-top__menu-links--active')
        }
    })

    // Filter
    $('.filter-btn').click(function () {
        $('.filter-content').toggleClass('filter-content--active')
        $('body').toggleClass('fixed-body')
    })

    $('.filter-close').click(function () {
        $('.filter-content').removeClass('filter-content--active')
        $('body').removeClass('fixed-body')
    })

    // File input
    function fileSize(size) {
        var i = Math.floor( Math.log(size) / Math.log(1024) );
        return ( size / Math.pow(1024, i) ).toFixed(2) * 1 + ' ' + ['B', 'kB', 'MB', 'GB', 'TB'][i];
    };

    var inputFile = document.getElementById('input-file');
    inputFile.addEventListener('change', inputHandler, false);

    function inputHandler() {
        document.getElementById('output').innerHTML = '';
        var element = document.getElementById(this.id),
            result = element.files;
        for (var x = 0; x < result.length; x++) {
            var file = result[x],
                li  = document.createElement("li");
            li.className = "list-group-item";
            li.innerHTML = "<strong>Имя Файла: </strong>" + file.name + "<br ><strong>Тип Файла: </strong> " + file.type + "<br ><strong>Размер Файла:  </strong>" + fileSize(file.size);
            document.getElementById('output').appendChild(li);
        }
    }



})

// Calendar
var Cal = function(divId) {
    //Сохраняем идентификатор div
    this.divId = divId;
    // Дни недели с понедельника
    this.DaysOfWeek = [
        'Пн',
        'Вт',
        'Ср',
        'Чтв',
        'Птн',
        'Суб',
        'Вск'
    ];
    // Месяцы начиная с января
    this.Months =['Январь', 'Февраль', 'Март', 'Апрель', 'Май', 'Июнь', 'Июль', 'Август', 'Сентябрь', 'Октябрь', 'Ноябрь', 'Декабрь'];
    //Устанавливаем текущий месяц, год
    var d = new Date();
    this.currMonth = d.getMonth();
    this.currYear = d.getFullYear();
    this.currDay = d.getDate();
};
// Переход к следующему месяцу
Cal.prototype.nextMonth = function() {
    if ( this.currMonth == 11 ) {
        this.currMonth = 0;
        this.currYear = this.currYear + 1;
    }
    else {
        this.currMonth = this.currMonth + 1;
    }
    this.showcurr();
};
// Переход к предыдущему месяцу
Cal.prototype.previousMonth = function() {
    if ( this.currMonth == 0 ) {
        this.currMonth = 11;
        this.currYear = this.currYear - 1;
    }
    else {
        this.currMonth = this.currMonth - 1;
    }
    this.showcurr();
};
// Показать текущий месяц
Cal.prototype.showcurr = function() {
    this.showMonth(this.currYear, this.currMonth);
};
// Показать месяц (год, месяц)
Cal.prototype.showMonth = function(y, m) {
    var d = new Date()
        // Первый день недели в выбранном месяце
        , firstDayOfMonth = new Date(y, m, 7).getDay()
        // Последний день выбранного месяца
        , lastDateOfMonth =  new Date(y, m+1, 0).getDate()
        // Последний день предыдущего месяца
        , lastDayOfLastMonth = m == 0 ? new Date(y-1, 11, 0).getDate() : new Date(y, m, 0).getDate();
    var html = '<table>';
    // Запись выбранного месяца и года
    html += '<thead><tr>';
    html += '<td colspan="7">' + this.Months[m] + ' ' + y + '</td>';
    html += '</tr></thead>';
    // заголовок дней недели
    html += '<tr class="days">';
    for(var i=0; i < this.DaysOfWeek.length;i++) {
        html += '<td>' + this.DaysOfWeek[i] + '</td>';
    }
    html += '</tr>';
    // Записываем дни
    var i=1;
    do {
        var dow = new Date(y, m, i).getDay();
        // Начать новую строку в понедельник
        if ( dow == 1 ) {
            html += '<tr>';
        }
        // Если первый день недели не понедельник показать последние дни предыдущего месяца
        else if ( i == 1 ) {
            html += '<tr>';
            var k = lastDayOfLastMonth - firstDayOfMonth+1;
            for(var j=0; j < firstDayOfMonth; j++) {
                html += '<td class="not-current">' + k + '</td>';
                k++;
            }
        }
        // Записываем текущий день в цикл
        var chk = new Date();
        var chkY = chk.getFullYear();
        var chkM = chk.getMonth();
        if (chkY == this.currYear && chkM == this.currMonth && i == this.currDay) {
            html += '<td class="today">' + i + '</td>';
        } else {
            html += '<td class="normal">' + i + '</td>';
        }
        // закрыть строку в воскресенье
        if ( dow == 0 ) {
            html += '</tr>';
        }
        // Если последний день месяца не воскресенье, показать первые дни следующего месяца
        else if ( i == lastDateOfMonth ) {
            var k=1;
            for(dow; dow < 7; dow++) {
                html += '<td class="not-current">' + k + '</td>';
                k++;
            }
        }
        i++;
    }while(i <= lastDateOfMonth);
    // Конец таблицы
    html += '</table>';
    // Записываем HTML в div
    document.getElementById(this.divId).innerHTML = html;
};
// При загрузке окна
window.onload = function() {
    // Начать календарь
    var c = new Cal("divCal");
    c.showcurr();
    // Привязываем кнопки «Следующий» и «Предыдущий»
    getId('btnNext').onclick = function() {
        c.nextMonth();
    };
    getId('btnPrev').onclick = function() {
        c.previousMonth();
    };
}
// Получить элемент по id
function getId(id) {
    return document.getElementById(id);
}